@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}
body {
  margin: 0;
  padding: 0;
}

p, h1, h2 , h3 {
  margin: 0;
}


.app {
  background-color: white;
  padding: 50px;
  min-height: 100vh;
}
.app-title {
  margin-top: 2rem;
  margin-bottom: 3rem;
  font-size: 3.5rem;
  text-align: center;
  color: rgb(12, 134, 182);
}
.app-content {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
  align-items: top;
}

.app h3 {
  text-align: center;
}
.alert {
    color: red;
}
  
.add-new-todo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: white;
    padding: 10px;
    border-radius: 10px;
    position: relative;
    border: 1px solid black;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    width: 300px;
    margin-right: 50px;
    margin-bottom: 50px;
}

.add-new-todo label {
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 5px;
}

.add-new-todo input {
    height: 30px;
    border: 1px solid black;
    width: 100%;
    border-radius: 10px;
    padding-left: 10px;
}

.add-new-todo select {
    height: 30px;
    border: 1px solid black;
    width: 100%;
    border-radius: 10px;
}

.add-new-todo .btn-wrap {
    margin-top: 20px;
}

.add-new-todo .btn-wrap button {
    height: 30px;
    padding: 0px 40px;
}

.add-new-todo span {
    font-weight: bold;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
th, td{
    padding: 1rem;
    font-weight: 700;
    border: 1px solid black;
    text-align: center;
}

.to-do-list {
    flex: 1 1;
}
.to-do-list table {
    background-color: white;
    border-collapse: collapse;
    width: 100%;
}
.btn-add {
    border: none;
    background-color: rgb(153, 216, 241);
    border-radius: 1rem;
    height: 40px;
    padding: 0px 20px;
    font-weight: bold;
    cursor: pointer;
    margin-bottom: 20px;
}

.btn-wrap {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.btn-wrap button {
    padding: 0.5rem 1.6rem;
    border-radius: 1rem;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 700;
    border: none;
    background-color: rgb(153, 216, 241);
}

.wrapper-filter {
    margin-top: 20px;
  }
  
.filter {
    cursor: pointer;
    background-color: #fff;
    padding: 5px 10px;
    /* border: 1px solid gray; */
    margin-right: 10px;
    border-radius: 15px;
    color: rgb(189, 189, 16);
    font-style: italic;
    font-size: 1.2rem;
  }
.to-do-list--top{
    display: flex;
    justify-content: space-between;
}
.wrapper-bottom .select_sort{
    padding: 0.5rem 1.6rem;
    border-radius: 1rem;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 700;
    color: #000;
    border: none;
}
